.portfolio {
    background-color: #191919;
}
.portfolio-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.portfolio-content .portfolio-card {
    height: 75vh;
    background-image: url(../images/85068cad88edf59af68bf05ae5c35d37.jpg);
    background-color: rgba(0, 0, 0, alpha);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 30%;
    margin: 15px;
    border-radius: 10px;
    top: 50%;
    position: relative;

}
.card-content {
    color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}
.card-content p {
    color: #9597A2;
    text-transform: capitalize;
    margin-bottom: 30px;
}